button {
  color: #fff;
  background: #dc143c;
  border: 0;
  border-radius: 4px;
  margin: 20px auto;
  padding: 6px 10px;
  display: block;
}

.popup-wrapper {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .5);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup {
  text-align: center;
  width: 100%;
  max-width: 300px;
  background: #fff;
  margin: 10% auto;
  padding: 20px;
  font-family: arial;
  position: relative;
}

.popup a {
  color: #fff;
  background: #dc143c;
  padding: 6px 10px;
  text-decoration: none;
}

.popup-close {
  cursor: pointer;
  position: absolute;
  top: 5px;
  right: 8px;
}

